<script>
export default {
  name: 'HelloWorld',
  data: () => {
    return {
      phone: "",
      refresh_cooldown: 0,
      msg_loading: true,
      submit_loading: false,
      tabcontrol: {
        tab: null,
        items: [
          {sid: 0, tab: "语文", content: "Tab 1 Content"},
          {sid: 1, tab: "数学", content: "Tab 2 Content"},
          {sid: 2, tab: "英语", content: "Tab 3 Content"},
          {sid: 3, tab: "物理", content: "Tab 4 Content"},
          {sid: 4, tab: "化学", content: "Tab 5 Content"},
          {sid: 5, tab: "生物", content: "Tab 6 Content"},
          {sid: 6, tab: "历史", content: "Tab 7 Content"},
          {sid: 7, tab: "政治", content: "Tab 8 Content"},
          {sid: 8, tab: "地理", content: "Tab 9 Content"},
        ],
      },
      submitdata: [
        {sid: 1, num: "??",who:[{name:"li",task:'b'},{name:"jj",task:'b'},{name:"ac",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},{name:"li",task:'b'},]},
        {sid: 2, num: "??"},
        {sid: 3, num: "??"},
        {sid: 4, num: "??"},
        {sid: 5, num: "??"},
        {sid: 6, num: "??"},
        {sid: 7, num: "??"},
        {sid: 8, num: "??"},
        {sid: 9, num: "??"},
      ],
      search: null,
      alertcontrol: {
        success: {state: false, text: ""},
        info: {state: false, text: ""},
        warn: {state: false, text: ""},
        error: {state: false, text: ""},
      },
      master_dialog:true,
      master_active_dialog:false,
      student_choose_dialog:true,
      is_master:false,
      master_pwd:'',
      selected_who:[],
      notify_list:[],
      notify_name:[],
    }
  },
  created() {
    this.refresh();
  },
  watch:{

  },
  methods: {
    reload: function () {
      location.reload(false);
    },
    jump: function (url) {
      window.location.href = url;
    },
    refresh: function () {
      // $.ajax({
      //   async: true,
      //   url: window.baseUrl + "type=info",
      //   success: function (data) {
      //     Layoutvue.$data.submitdata = jQuery.parseJSON(data);
      //     console.log(Layoutvue.$data.submitdata);
      //     Layoutvue.$data.msg_loading = false;
      //   },
      // });
      this.$axios.get(window.baseUrl + "type=info").then(resolve=>{
        this.submitdata=JSON.parse(resolve.data);
        this.msg_loading=false;
      })
    },
    showDialog: function (text, type) {
      this.alertcontrol[type].state = true;
      this.alertcontrol[type].text = text;
    },
    requestMaster(action,body){
      this.$axios({url: window.baseUrl+`type=master&p=${this.master_pwd}&a=${action}`,method:'get',data:body}).then(resolve=>{
        const data=resolve.data;
        if(data['state']==='ok'){
          action==="on"&&(this.showDialog(data['msg'],'success'),this.master_dialog=true,this.master_active_dialog=false)
        }
        else{
          this.showDialog(data['msg'],'error');
        }
      })
    },
    addNotifyList(who){
      if (this.notify_name.indexOf(who['name'])===-1){
       this.notify_list.push(who);
       this.notify_name.push(who['name']);
      }
    },
    doNotify(){
      this.requestMaster('notify',JSON.stringify(this.notify_name))
    },
    doSubmit(){
      this.requestMaster('submit',`${this.notify_list.join(",")}\n上述同学请交下作业哦！`)
    }

  },


}
</script>
